.social__list {
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.social__link {
  display: block;
  width: 46px;
  height: 48px;
  text-align: center;
  background-color: $lightblue;
  padding: 14px;

    &:hover {
      background-color: $lightblue-hover;
  }
    &:active {
      background-color: $lightblue-focus;
  }
    &:active .social__link-icon {
      opacity: 0.3;
  }
}

.social__link--facebook {
    margin-left: 5px;
    margin-right: 5px;
}
